<template>
  <div>

  </div>
</template>

<script>
import Bus from "../../../utils/Bus";
export default {
name: "CommunityBuilderSetInfo",
  data(){
     return{
       communityBuilderInfo:{},
     }
  },
  destroyed(){
    Bus.$off('globalMsgForCommunityBuilder');
    console.info("CommunityBuilderSetInfo destroyed");
  },
  created() {
    var that = this;
    console.info("CommunityBuilderSetInfo created");
    Bus.$on('globalMsgForCommunityBuilder',function(val){// globalMsg 
      console.log(""+JSON.stringify(val));
      that.communityBuilderInfo = val;

    });
  },
  methods:{
    showPublicInfo:function (){
      console.info("this.communityBuilderInfo:"+JSON.stringify(this.communityBuilderInfo));
      return this.communityBuilderInfo;

    }
  }

}
</script>

<style scoped>

</style>



